import nullData from '../../components/lists/nullData.vue';
import uniCountdown from '@dcloudio/uni-ui/lib/uni-countdown/uni-countdown.vue';
export default {
	onLoad() {
		this.menuButtonInfo = wx.getMenuButtonBoundingClientRect();
		this.diyHeight = this.menuButtonInfo.height;
		this.initTime =  this.toTimeStamp(this.toFullTime()) / 1000;
	},
	data() {
		return {
			ndConfig: {
				tip: '暂无秒杀活动～',
				bgColor: '#fff'
			},
			isShowAct: true,
			diyHeight: 0,
			menuButtonInfo: 0,
			diy: {
				title: '秒杀活动'
			},
			currentIndex: 0,
			timeTab: [],
			goodsList: [],
			downSecond: 0,
			dwonMs: 0,
			initTime: 0
		};
	},
	methods: {
		changeTab(e) {
			// let index = e.currentTarget.dataset.index;
			// this.currentIndex = index;
		},
		toTimeStamp(t) {
			t = t.replace(/-/g, '/');
			let date = new Date(t);
			return date.getTime();
		},
		toTime(ms) {
			let s = Math.floor(ms / 1000);
			this.downSecond = s;
		},
		toFullTime() {
			let [year, month, day, h, m, s, time] = [0, 0, 0, 0, 0];
			let data = new Date();
			year = data.getFullYear();
			month = data.getMonth() + 1;
			day = data.getDate().toString().padStart(2,'0');
			h = data.getHours().toString().padStart(2,'0');
			m = data.getMinutes().toString().padStart(2,'0');
			s = data.getSeconds().toString().padStart(2,'0');
			time = `${year}-${month}-${day} ${h}:${m}:${s}`;
			return time;
		},
		toDetail(e) {
			let index = e.currentTarget.dataset.index;
			let id = this.goodsList[index].goods_id;
			uni.navigateTo({
				url: `/pages/goods/detail?id=${id}`
			});
		}
	},
	mounted() {
		this.$http({
			aurl: 'seckill',
			success: res => {
				let data = res.data.result;
				if (data.sec_kill_list.length > 0) {
					console.log(data)
					this.timeTab = data.sec_kill_list;
					this.goodsList = data.now_activiy_goods_list;
					let end = this.timeTab[this.currentIndex].end_time;
					console.log(end)
					this.dwonMs = this.toTimeStamp(end) - this.toTimeStamp(this.toFullTime());
					this.toTime(this.dwonMs);
				} else {
					this.currentIndex = -1;
					this.isShowAct = false;
				}
			},
			fail: err => {
				console.log(err);
			}
		});
	},
	components: {
		uniCountdown,
		nullData
	}
};
